body {
	font-family: "Open Sans", sans-serif;
	/* padding: 1em; */
	background-color: #F2F2F2;
}

/* Align blocks */
.container {
	display: flex;
	flex-direction: column;
}

.search-header {
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	padding: 100px 100px;
	background-image: url("https://external-content.duckduckgo.com/iu/?u=https%3A%2F%2Fwallpaperaccess.com%2Ffull%2F266471.jpg&f=1&nofb=1");
	margin-bottom: 40px;
}

.search-logo {
	flex: 1;
	text-align: right;
	padding-right: 5%;
	font-size: 65px;
	font-family: "Open Sans", sans-serif;
	font-weight: 800;
	color: rgb(95, 196, 240)
}

.search-bar {
	flex: 2;
}

.content { 
	display: flex;
	flex-direction: row;
	justify-content: center;
}

/* Style search input */
#search-input input {
    transition: border-color 0.3s ease-in;
	outline: 0;
	border-radius: 50px;
}

input[type="search"]:focus {
    transition: border-color 0.3s ease-in;
	outline: 0;
	border-radius: 50px;
}

#search-input input {
    padding-left: 100px;
    font-weight: 100;
    padding: 10px 10px 5px 35px;
    border: none;
    border-bottom: 5px solid #eee;
    appearance: none;
	-webkit-appearance: none;
	border-radius: 50px;
	font-family: "Open Sans", sans-serif;
	width: 50%;
}

.ais-SearchBox-input {
    width: 50%;
    -webkit-appearance: none;
    appearance: none;
    border: 1px solid #ccc;
    border-radius: 50px;
    font-size: 18px;
    padding: 20px 20px;
}

/* Style search button */
.ais-SearchBox-submit {
	position: relative;
	border: none;
	background-color: transparent;
	right: 50px;
}

.ais-SearchBox-submitIcon {
	width: 20px;
	height: 20px;
}

.ais-SearchBox-reset {
	visibility: hidden;
}

.ais-RefinementList-checkbox:checked  {
	background: #2aa1c0;
}

.ais-RefinementList-count {
	float: right;
    font: 400 15px "Open Sans";
}

input[type="checkbox"] {
    background-color: rgba(192, 192, 192, 0.863);
    cursor: default;
    -webkit-appearance: checkbox;
    box-sizing: border-box;
    margin: 3px 3px 3px 4px;
	border: initial;
}

/* Remove x button */
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration { display: none; }

/* Style left side */
#left-side {
	flex: 1;
	padding-left: 310px;
}

#filter-header {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

#right-side {
	flex: 4
}

/* Style cards */
#card {
	display: flex;
	text-decoration: none;
	padding: 20px 20px;
	width: 100%;
}

#card-content {
	display: flex;
	padding-left: 15px;
}

.ais-Hits-item {
	list-style-type: none;
}

.ais-RefinementList-list {
	list-style-type: none;
	padding-left: 4px;
	padding-top: 0px;
}

.ais-RefinementList-item {
	font-family: "Open Sans", sans-serif;
	line-height: 1.6rem;
	margin-bottom: 5px;
}

.ais-ClearRefinements {
	padding: 20px 0px;
	margin: 0px 0px;
	border-radius: 14px;
}

.ais-ClearRefinements-button {
	border-radius: 10px;
	outline: 0;
	box-shadow: none;
	background-color: transparent;
    font: 400 13.3333px "Open Sans";
}

/* Style hits */
.ais-Hits-item {
	margin-bottom: 1em;
	width: calc(60% - 1rem);
}

.ais-Hits-item img {
	margin-right: 1em;
	width: 200px;
}


.hit-name {
	margin-bottom: 0.5em;
	line-height: 1.7rem;
	font-weight: 600;
	/* font-size: 18px; */
}

.hit-shortDescription {
	color: #888;
	font-size: 14px;
	margin-bottom: 1.5em;
	line-height: 1.3rem;
}

.hit-salePrice {
	background-color: #EB2531;
	color: white;
	width: auto;
	padding: 5px 9px;
	font-weight: 600;
	border-radius: 4px;
	margin-left: 10px;
}


/* Style pagination */
#pagination {
	margin-top: 60px;
  }

.ais-Pagination-list {
	font-size: 0;
	list-style-type: none;
	text-align: center;
	display: block;
	margin: 60px 0px;
  }
  
.ais-Pagination-item {
	font-size: 16px;
	display: inline;
  }

.ais-Pagination-link {
	padding: 8px 12px;
	text-decoration: none;
	color: #000;
	border: 1px solid #eee;
  }

	
.ais-Pagination-item {
	padding: 3px;
  }
  
.ais-Pagination-item:first-child {
	border-top-left-radius: 4px;
	border-bottom-left-radius: 4px;
  }

.ais-Pagination-item:last-child {
	border-right: 1px solid #eee;
	border-top-right-radius: 4px;
	border-bottom-right-radius: 4px;
  }

  .ais-Pagination-item--selected a {
	color: white;
	border-color: #02A7F0;
	background: #02A7F0;
  }

  .ais-Pagination-item--selected a:hover {
	cursor: default;
  }

  .ais-Pagination-item--disabled {
	cursor: not-allowed;
  }

  .ais-Pagination-item--disabled:hover {
	background: none;
}